import React from 'react';
import './Homepage.css';
import pictureOfYou from './your-picture.jpg';
import { Link, useLocation } from 'react-router-dom';

import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import ContactMailIcon from '@mui/icons-material/ContactMail';

const Homepage = () => {
    
    const location = useLocation();
    const getIconColor = (iconUrl) => {
        if (iconUrl === location.pathname) {
          return '#745E5A';
        }
        return '#3a243b';
      };

    return (
        <div className="homepage">
            <h1 className="homepagename">Thomas Pollard</h1>
            <img src={pictureOfYou} alt="Your Name" className="homepagepicture" />
            <div className="homepagebubbles">
                    <Link to="/PersonalWebsite/" className="homepagebubble"><HomeIcon style={{ color: getIconColor('/PersonalWebsite/'),  width: "3rem", height: "3rem" }}/></Link>
                    <Link to="/about" className="homepagebubble"><InfoIcon style={{ color: getIconColor('/about'),  width: "3rem", height: "3rem" }}/></Link>
                    <Link to="/contact" className="homepagebubble"><ContactMailIcon style={{ color: getIconColor('/contact'),  width: "3rem", height: "3rem" }}/></Link>
             </div>
        </div>
    );
};

export default Homepage;