import React from 'react';
import './About.css';
import { Link, useLocation } from 'react-router-dom';
import Card from 'react-bootstrap/Card'

import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import ContactMailIcon from '@mui/icons-material/ContactMail';

const About = () => {
    const location = useLocation();
    const getIconColor = (iconUrl) => {
        if (iconUrl === location.pathname) {
          return '#745E5A';
        }
        return '#3a243b';
      };

    return (
        <div className="about-page">
            <h1 className="aboutpagename">About</h1>
            <div className="row">
                <div className="col-sm-6">
                <Card className ="card">
                    <Card.Body>
                        <Card.Title>Quick Info About This Website!</Card.Title>
                         <Card.Text>
                             This Website is a test for some basic react navigation using Link from react-router. The icons will change color to show the page has become 'active'.
                            </Card.Text>
                    </Card.Body>
                </Card>
                </div>
                <div className="col-sm-6">
                </div>
                </div>
            <div className="about-page__icons">
                 <Link to="/PersonalWebsite/" className="about-page__icon"><HomeIcon  style={{ color: getIconColor('/'),  width: "3rem", height: "3rem" }} /></Link>
                 <Link to="/about" className="about-page__icon"><InfoIcon  style={{ color: getIconColor('/about'),  width: "3rem", height: "3rem" }} /></Link>
                 <Link to="/contact" className="about-page__icon"><ContactMailIcon style={{ color: getIconColor('/contact'),  width: "3rem", height: "3rem" }}  /></Link>
            </div>
        </div>
    );
};

export default About;