import React from 'react';
import './Contact.css';
import { Link, useLocation } from 'react-router-dom';
import Resume from './Resume.pdf'
import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import Card from 'react-bootstrap/Card'

const Contact = () => {
    const location = useLocation();
    const getIconColor = (iconUrl) => {
        if (iconUrl === location.pathname) {
          return '#745E5A';
        }
        return '#3a243b';
      };

    return (
        <div className="about-page">
            <h1 className="contactpagename">Contact</h1>
            <div className="row">
                <div className="col-sm-6">
                <Card className ="card">
                    <Card.Body>
                         <Card.Text>
                             Use the button to download my resume!
                            </Card.Text>
                    </Card.Body>
                </Card>
                </div>
                </div>
            <a href={Resume} download>
                <button>Download File</button>
            </a>
            <div className="about-page__icons">
                 <Link to="/PersonalWebsite/" className="about-page__icon"><HomeIcon  style={{ color: getIconColor('/'),  width: "3rem", height: "3rem" }} /></Link>
                 <Link to="/about" className="about-page__icon"><InfoIcon  style={{ color: getIconColor('/about'),  width: "3rem", height: "3rem" }} /></Link>
                 <Link to="/contact" className="about-page__icon"><ContactMailIcon style={{ color: getIconColor('/contact'),  width: "3rem", height: "3rem" }}  /></Link>
            </div>
        </div>
    );
};

export default Contact;